@font-face {
	font-family: 'SocMed';
	src:url('../fonts/SocMed.eot');
	src:url('../fonts/SocMed.eot') format('embedded-opentype'),
		url('../fonts/SocMed.woff') format('woff'),
		url('../fonts/SocMed.ttf') format('truetype'),
		url('../fonts/SocMed.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
.socmed {
	font-family: 'SocMed';
	// speak-as: ;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

