.stage {
    @include section();
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    
    background-color: $primaryColor;
    
    &__wrapper {
        display: flex;
        flex-direction: column-reverse;
        
        @media ($tablet) {
            flex-direction: row;;
            justify-content: space-between;
        }
        
        @media ($desktop) {
            padding-left: 0;
            padding-right: 0;
        }
    }

    &__title {
        @include h1();
    }

    &__image {
        @include img($white);
        width: 100%;
        height: 21.4rem;

        @media ($tablet) {
            max-width: 300px;
            margin-bottom: 0;       
        }
        
        @media ($desktop) {
            max-width: 469px;
        }
    }
 
    &__list {
        @include lead();

        &-item{
        padding-left: 0.5rem;
        list-style: url("../images/check_icon.png");
        }
    }
    
}