@mixin h1($color: $white) {
    font-size: 4rem;
    color: $color;
    font-family: $mainFont;
    text-shadow: $mainShadow;
    margin-bottom: 5rem;
    
    @media ($tablet) {
        font-size: 5.9rem;
        margin-bottom: 3rem;
    }
}

@mixin h2($color: $primaryColor, $align: center) {
    color: $color;
    font-size: 3rem;
    text-align: $align;
    line-height: 1.2;
    margin-bottom: 3rem;
}

@mixin h3($color: $primaryColor) {
    color: $color; 
    font-size: 2.4rem;
    line-height: 1.25;
}

@mixin p1($color: $textColor) {
    color: $color; 
    font-size: 1.6rem;
    line-height: 1.5;
}
@mixin p2($color: $textColor) {
    color: $color; 
    font-size: 1.4rem;
    line-height: 1.29;
} 

@mixin lead($color: $white) {
    color: $color; 
    font-size: 2rem;
    line-height: 1.8;
    text-shadow: $mainShadow;
}

@mixin img($bg: $primaryColor) {
    background-color: $bg;
    object-fit: cover;
    object-position: center;
}

@mixin section($pt: 5rem, $pb: 5rem) {
     padding-top: $pt;
     padding-bottom: $pb;
}

%input {
    color: $secondaryColor;
    font-size: 1.6rem;
    background-color: $white;
    color: $secondaryColor;
    padding: 1.4rem 1rem;
    line-height: 1;
    border-radius: 0.5rem;
    border: none;
}

%textarea {
    @extend %input;
    resize: none;
    height: 7rem;
}


%btn {
    display: inline-block;
    border-radius: 5px;
    background-color: $white;
    color: $primaryColor;
    text-transform: uppercase;
    font-size: 1.8rem;
    font-weight: bold;
    line-height: 1;
    padding: 1.4rem 1.8rem;
    cursor: pointer;
    border: none;
    text-align: center;
    user-select: none;
    text-decoration: none;

    &:hover {
        background-color: darken($color: $white, $amount: 10%);
    }

    &:focus {
        outline: none;
    }
}  